import React, {Component} from "react";
import RecFilmsLogo from "../../../assets/img/RecFilmsLogo.png";

class NavBar extends Component{

    render() {
        return (
            <nav className={"navbar navbar-expand-lg navbar-dark fixed-top scrolling-navbar"}>
                <div className="container">
                    <a className="navbar-brand" href="#intro">
                        <img src={RecFilmsLogo} alt="RecFilms" width="170px"/>
                    </a>

                    <button className="navbar-toggler" type="button" data-toggle="collapse"
                            data-target="#customNav"
                            aria-controls="customNav" aria-expanded="false"
                            aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className={"collapse navbar-collapse"}>
                        {/*Nav Links*/}
                        <ul className={"navbar-nav mr-auto"}>
                            <li className="nav-item">
                                <a className="nav-link" href="#intro">Home</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#services">Servicios</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#examples">Ejemplos</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#gallery">Galería</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link"
                                   href="https://koskou.github.io/rec-films">Contacto</a>
                            </li>
                        </ul>

                        {/*Social Icons*/}
                        <ul className={"navbar-nav nav-flex-icons"}>
                            <li className={"nav-item"}>
                                <a href="https://www.facebook.com/Recfilms4k-979441162251157" target="_blank" className={"nav-link"}>
                                    <i className={"fab fa-facebook fa-2x blue-text"}></i>
                                </a>
                            </li>
                            <li className="nav-item">
                                <a href="#" className="nav-link">
                                    <i className="fab fa-instagram fa-2x orange-text"></i>
                                </a>
                            </li>
                            <li className="nav-item">
                                <a href="#" className="nav-link">
                                    <i className="fab fa-twitter fa-2x blue-text"></i></a>
                            </li>
                            <li className="nav-item">
                                <a href="#" className="nav-link">
                                    <i className="fab fa-youtube fa-2x red-text"></i></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        )
    }
}

export default NavBar;