import React, {Component} from "react";
import RecFilmsLogo from "../../../assets/img/RecFilmsLogo.png"

class Mask extends Component{

    render() {
        return (
            <div id="intro" className={"view"}>
                <div className={"mask rgba-black-strong"}>
                    <div className={"container-fluid d-flex align-items-center justify-content-center h-100"}>
                        <div className={"row d-flex justify-content-center text-center"}>
                            <div className={"col-md-10"}>
                                <div className={"text-center"}>
                                    <img src={RecFilmsLogo} alt="RecFilms"
                                         className={"img-fluid"}/>
                                </div>
                                <hr className={"hr-light"}/>

                                <h4 className={"white-text my-4"}>Calidad y acompañamiento
                                        personalizado</h4>
                                <a href="https://koskou.github.io/react-deploy/">
                                    <button type="button" className={"btn btn-outline-white"}>
                                        Ver Servicios
                                        <i className={"fas fa-book ml-2"}></i>
                                    </button>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
        </div>
        )
    }

}

export default Mask;